import { Context, createContext, useContext, useEffect, useState } from "react"
import { useNuiEvent } from "../hooks/useNuiEvent"
import { debugData } from "../utils/debugData"

debugData([
  {
    action: "loadLocales",
    data: ["en", "vi"],
  },
])

debugData([
  {
    action: "setLocale",
    data: {
      language: "Ting Vit",
      ui: {
        server_name: "TEDDY",
        disclaimer: "GII THIU",
        server_description:
          " Server RP Teddy l ni bn c c hi tham gia vo cuc sng o trong th gii GTA 5 vi cc vai tr a dng. Ti y, bn c th xy dng nhn vt ca mnh, tng tc vi cng ng, v tham gia vo cu chuyn y kch tnh. Hy tham gia Teddy RP ngay  tri nghim s hp dn ca cuc phiu lu trong mt th gii o c o.",
        loading: "Loading",
        loading_map: "Ti Map",
        loading_resources: "Ti d liu",
        loading_scripts: "Ti tnh nng",
      },
    },
  },
])

interface Locale {
  language: string
  ui: {
    server_name: string
    disclaimer: string
    server_description: string
    loading: string
    loading_map: string
    loading_resources: string
    loading_scripts: string
  }
}

interface LocaleContextValue {
  locale: Locale
  setLocale: (locales: Locale) => void
}

const LocaleCtx = createContext<LocaleContextValue | null>(null)

const LocaleProvider: React.FC<{ children: React.ReactNode }> = ({
  children,
}) => {
  const [locale, setLocale] = useState<Locale>({
    language: "",
    ui: {
      server_name: "",
      disclaimer: "",
      server_description: "",
      loading: "",
      loading_map: "",
      loading_resources: "",
      loading_scripts: "",
    },
  })
  useEffect(() => {
    // Load the appropriate JSON file based on your desired locale
    // For example, if you want English (en.json):
    import("../locales/vi.json")
      .then((localeData) => {
        setLocale(localeData)
      })
      .catch((error) => {
        console.error("Error loading locale:", error)
      })
  }, [])
  return (
    <LocaleCtx.Provider value={{ locale, setLocale }}>
      {children}
    </LocaleCtx.Provider>
  )
}

export default LocaleProvider

export const useLocales = () =>
  useContext<LocaleContextValue>(LocaleCtx as Context<LocaleContextValue>)
